import Vue from "vue";
import Vuex from "vuex";
import { db } from "../firebase";
import { getDocs, collection } from "@firebase/firestore";
import { writeBatch, doc, getDoc } from "@firebase/firestore"; 

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userCart: [] as {
      ProductName: string,
      UnitPrice: number,
      Qty: number,
      TotalCost: number,
      VAT: number
    }[],

    consumptionCart: [] as {
      ProductName: string,
      UnitPrice: number,
      Qty: number,
      TotalCost: number,
      VAT: number
    }[],

    dispatchCart: [] as {
      ProductName: string,
      UnitPrice: number,
      Qty: number,
      TotalCost: number,
      VAT: number
    }[],

    invoiceCart: [] as {
      ProductName: string,
      UnitPrice: number,
      Qty: number,
      TotalCost: number,
      VAT: number
    }[],

    cart: [] as {
      ProductName: string,
      UnitPrice: number,
      Qty: number,
      TotalCost: number,
      VAT: number
    }[],

    productInventory: [] as {
      ProductName: string,
      SellingPrice: number,
      Img: string
      Branch: string,
      Quantity: number,
      Tax: number,
      Alert: number,
    }[],
  },
  getters: {
    cart: state => state.cart,
    userCart: state => state.userCart,
    invoiceCart: state => state.invoiceCart,
    dispatchCart: state => state.dispatchCart,
    consumptionCart: state => state.consumptionCart,
    productInventory: state => state.productInventory,

    totalCost: (state) => {
      return state.cart.reduce((sum, item) => sum + item.TotalCost, 0);
    },

    totalCostUser: (state) => {
      return state.userCart.reduce((sum, item) => sum + item.TotalCost, 0);
    },

    totalCostInvoice: (state) => {
      return state.invoiceCart.reduce((sum, item) => sum + item.TotalCost, 0);
    },

    totalCostDispatch: (state) => {
      return state.dispatchCart.reduce((sum, item) => sum + item.TotalCost, 0);
    },

    totalCostConsumption: (state) => {
      return state.consumptionCart.reduce((sum, item) => sum + item.TotalCost, 0);
    },
  },
  
  mutations: {
    pushProductTouserCart(state, product) {
      const index = state.userCart.findIndex(p => p.ProductName === product.ProductName);

      if (index !== -1) {
        // If the product already exists in the cart, replace the existing product with the updated one
        state.userCart.splice(index, 1, product);
      } else {
        // If the product does not exist in the cart, add it to the end of the array
        state.userCart.push(product);
      }
    },

    pushProductToCart(state, product) {
      const index = state.cart.findIndex(p => p.ProductName === product.ProductName);

      if (index !== -1) {
        // If the product already exists in the cart, replace the existing product with the updated one
        state.cart.splice(index, 1, product);
      } else {
        // If the product does not exist in the cart, add it to the end of the array
        state.cart.push(product);
      }
    },

    pushProductToInvoiceCart(state, product) {
      const index = state.invoiceCart.findIndex(p => p.ProductName === product.ProductName);

      if (index !== -1) {
        // If the product already exists in the cart, replace the existing product with the updated one
        state.invoiceCart.splice(index, 1, product);
      } else {
        // If the product does not exist in the cart, add it to the end of the array
        state.invoiceCart.push(product);
      }
    },

    pushProductToDispatchCart(state, product) {
      const index = state.dispatchCart.findIndex(p => p.ProductName === product.ProductName);

      if (index !== -1) {
        // If the product already exists in the cart, replace the existing product with the updated one
        state.dispatchCart.splice(index, 1, product);
      } else {
        // If the product does not exist in the cart, add it to the end of the array
        state.dispatchCart.push(product);
      }
    },


    pushProductToConsumptionCart(state, product) {
      const index = state.consumptionCart.findIndex(p => p.ProductName === product.ProductName);

      if (index !== -1) {
        // If the product already exists in the cart, replace the existing product with the updated one
        state.consumptionCart.splice(index, 1, product);
      } else {
        // If the product does not exist in the cart, add it to the end of the array
        state.consumptionCart.push(product);
      }
    },

    setProductInventory(state, productInventory) {
      state.productInventory = productInventory;
    },

    UPDATE_QUANTITY(state, { item, quantity, totalcost }) {
      // Find the index of the item in the cart
      const index = state.cart.findIndex(i => i.ProductName === item.ProductName);
      // Update the quantity of the item in the cart
      state.cart[index].Qty = quantity;
      state.cart[index].TotalCost = totalcost;
    },

    UPDATE_USERQUANTITY(state, { item, quantity, totalcost }) {
      // Find the index of the item in the cart
      const index = state.userCart.findIndex(i => i.ProductName === item.ProductName);
      // Update the quantity of the item in the cart
      state.userCart[index].Qty = quantity;
      state.userCart[index].TotalCost = totalcost;
    },

    UPDATE_INVOICEQUANTITY(state, { item, quantity, totalcost }) {
      // Find the index of the item in the cart
      const index = state.invoiceCart.findIndex(i => i.ProductName === item.ProductName);
      // Update the quantity of the item in the cart
      state.invoiceCart[index].Qty = quantity;
      state.invoiceCart[index].TotalCost = totalcost;
    },

    UPDATE_DISPATCHQUANTITY(state, { item, quantity, totalcost }) {
      // Find the index of the item in the cart
      const index = state.dispatchCart.findIndex(i => i.ProductName === item.ProductName);
      // Update the quantity of the item in the cart
      state.dispatchCart[index].Qty = quantity;
      state.dispatchCart[index].TotalCost = totalcost;
    },

    UPDATE_CONSUMPTIONQUANTITY(state, { item, quantity, totalcost }) {
      // Find the index of the item in the cart
      const index = state.consumptionCart.findIndex(i => i.ProductName === item.ProductName);
      // Update the quantity of the item in the cart
      state.consumptionCart[index].Qty = quantity;
      state.consumptionCart[index].TotalCost = totalcost;
    },

    REMOVE_ITEM(state, item) {
      // Find the index of the item in the cart array
      const index = state.cart.indexOf(item)
  
      // Remove the item from the cart array
      state.cart.splice(index, 1)
    },

    REMOVE_USERITEM(state, item) {
      // Find the index of the item in the cart array
      const index = state.userCart.indexOf(item)
  
      // Remove the item from the cart array
      state.userCart.splice(index, 1)
    },

    REMOVE_INVOICEITEM(state, item) {
      // Find the index of the item in the cart array
      const index = state.invoiceCart.indexOf(item)
  
      // Remove the item from the cart array
      state.invoiceCart.splice(index, 1)
    },

    REMOVE_DISPATCHITEM(state, item) {
      // Find the index of the item in the cart array
      const index = state.dispatchCart.indexOf(item)
  
      // Remove the item from the cart array
      state.dispatchCart.splice(index, 1)
    },

    REMOVE_CONSUMPTIONITEM(state, item) {
      // Find the index of the item in the cart array
      const index = state.consumptionCart.indexOf(item)
  
      // Remove the item from the cart array
      state.consumptionCart.splice(index, 1)
    },
  },

  actions: {
    async saveDraftToFirestore({ state }) {
      // Get a new write batch
      const batch = writeBatch(db);
        
        for (const item of state.cart) {
          const draftDoc = doc(collection(db, "SalesDraft"))
          batch.set(draftDoc, {item,
          Status: "Draft", 
          Date: new Date()}); // Add the item data to the document
          
        }
        await batch.commit(); // Save the batch to the database
      
    },

    async saveConsumptionToFirestore({ state }) {
      // Get a new write batch
      const batch = writeBatch(db);

      for (const item of state.consumptionCart) {
          const consumptionDoc = doc(collection(db, "RawMaterialConsumption"))
          batch.set(consumptionDoc, {item,
          Date: new Date()}); // Add the item data to the document
          
        }
        await batch.commit(); // Save the batch to the database
      
    },

    async saveCartToFirestore({ state }) {
      // Get a new write batch
      const batch = writeBatch(db);
      let lastReceiptNumber = 0;

      //Get the last invoice number from the database
      const lastReceiptNumberDoc = await getDoc(doc(db, "Receipt", "ReceiptNumber"));
      
      if (lastReceiptNumberDoc.exists()) {
        lastReceiptNumber = lastReceiptNumberDoc.data().number;
        // Increment the last invoice number by 1
        const receiptNumber = lastReceiptNumber + 1;

        // Add the invoice number to the batch
        batch.set(doc(collection(db, "Receipt"), "ReceiptNumber"), { number: receiptNumber });
        for (const item of state.cart) {
          const salesDoc = doc(collection(db, "Sales"))
          batch.set(salesDoc, {item, 
          Receipt: receiptNumber,
          Status: "Completed",
          PaymentMethod: "Cash",
          Date: new Date()}); // Add the item data to the document
        }
        await batch.commit(); // Save the batch to the database
      } else {
        const receiptNumber = 1;

        // Add the invoice number to the batch
        batch.set(doc(collection(db, "Receipt"), "ReceiptNumber"), { number: receiptNumber });
        for (const item of state.cart) {
          const salesDoc = doc(collection(db, "Sales"))
          batch.set(salesDoc, {item, 
          ReceiptNo: receiptNumber,
          Status: "Completed",
          PaymentMethod: "Cash",
          Date: new Date()}); // Add the item data to the document
        }
        await batch.commit(); // Save the batch to the database
      }
    },

    async getInventory({ commit }) {
      const snapshot = await getDocs(collection(db, "Inventory"));
      const productInventory = snapshot.docs.map((doc) => doc.data());
      commit("setProductInventory", productInventory);
    },

    addProductToCart (context, product) {
      context.commit('pushProductToCart', product);
    },

    addProductToInvoiceCart (context, product) {
      context.commit('pushProductToInvoiceCart', product);
    },

    addProductToDispatchCart (context, product) {
      context.commit('pushProductToDispatchCart', product);
    },

    addProductToConsumptionCart (context, product) {
      context.commit('pushProductToConsumptionCart', product);
    },

    updateQuantity({ commit }, { item, quantity, totalcost }) {
      commit('UPDATE_QUANTITY', { item, quantity, totalcost });
    },

    updateInvoiceQuantity({ commit }, { item, quantity, totalcost }) {
      commit('UPDATE_INVOICEQUANTITY', { item, quantity, totalcost });
    },

    updateDispatchQuantity({ commit }, { item, quantity, totalcost }) {
      commit('UPDATE_DISPATCHQUANTITY', { item, quantity, totalcost });
    },

    updateConsumptionQuantity({ commit }, { item, quantity, totalcost }) {
      commit('UPDATE_CONSUMPTIONQUANTITY', { item, quantity, totalcost });
    },

    removeItem(context, item) {
      context.commit('REMOVE_ITEM', item)
    },

    removeInvoiceItem(context, item) {
      context.commit('REMOVE_INVOICEITEM', item)
    },

    removeDispatchItem(context, item) {
      context.commit('REMOVE_DISPATCHITEM', item)
    },

    removeConsumptionItem(context, item) {
      context.commit('REMOVE_CONSUMPTIONITEM', item)
    },
  },
  modules: {},
});
