<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background}">
    <SideBar/>
    <TopBar/>
    <v-container fluid >
      <template>
          <v-data-table :headers="headers" :items="desserts" :search="search" sort-by="name" class="elevation-1" >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title>Inventory Records</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn outlined color="primary" dark class="mb-2" v-bind="attrs" v-on="on"> ADD INVENTORY</v-btn>
                  </template>
                  <v-card>
                    <v-card-title>
                      <span class="text-h5">{{ formTitle }}</span>
                    </v-card-title>
                    <v-card-text>
                      <v-container style="background-color: white !important;">
                        <v-row>
                          <v-col cols="12" md="8">
                            <v-select v-model="editedItem.pname" :items="products" required label="Product Name"></v-select>
                          </v-col>
                          <v-col cols="4" style="padding-top: 25px !important;">
                            <v-btn outlined @click="productdialog = !productdialog">ADD NEW</v-btn>
                            <v-dialog v-model="productdialog" max-width="500px">
                              <v-card>
                                <v-card-title>
                                  ADD NEW PRODUCT NAME
                                </v-card-title>
                                  <v-card-text>
                                  <v-text-field v-model="newProductName" required label="New Product Name"></v-text-field>
                                  </v-card-text>
                                <v-card-actions>
                                  <v-btn color="primary" text @click="productdialog = false">Close</v-btn>
                                  <v-btn color="success" text @click="newProductCB()">Save</v-btn>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-col>
                          <v-col cols="12" md="8">
                          <v-select v-model="editedItem.branch" label="Branch Name" :items="branches" required></v-select>
                          </v-col>
                          <v-col cols="4" style="padding-top: 25px !important;">
                            <v-btn outlined @click="branchdialog = !branchdialog">ADD NEW</v-btn>
                            <v-dialog v-model="branchdialog" max-width="500px">
                              <v-card>
                                <v-card-title>
                                  ADD NEW BRANCH NAME
                                </v-card-title>
                                <v-card-text>
                                  <v-text-field v-model ="newBranch" label="New Branch Name" required></v-text-field>
                                </v-card-text>
                                <v-card-actions>
                                  <v-btn color="primary" text @click="branchdialog = false">Close</v-btn>
                                  <v-btn color="success" text @click="newBranchCB()">Save</v-btn>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-col>
                          <!--v-col cols="12" sm="6" md="4"-->
                            <v-text-field v-model="editedItem.quantity" label="Quantity/Units" required></v-text-field>
                          <!--/v-col-->
                          <!--v-col cols="12" sm="6" md="4"-->
                            <v-text-field v-model="editedItem.alert" label="Alert Level" required></v-text-field>
                          <!--/v-col-->
                          <!--v-col cols="12" sm="6" md="4"-->
                            <v-text-field v-model="editedItem.price" label="Selling Price" required></v-text-field>
                          <!--/v-col-->
                          <v-col cols="12" md="8">
                            <v-select v-model="editedItem.tax" label="Tax Rate" :items="taxes" required></v-select>
                          </v-col>
                          <v-col cols="4" style="padding-top: 25px !important;">
                            <v-btn outlined @click="taxdialog = !taxdialog">ADD NEW</v-btn>
                            <v-dialog v-model="taxdialog" max-width="500px">
                              <v-card>
                                <v-card-title>
                                  ADD TAX RATE
                                </v-card-title>
                                <v-card-text>
                                  <v-text-field v-model="newTax" label="New Tax Rate" required></v-text-field>
                                </v-card-text>
                                <v-card-actions>
                                  <v-btn color="primary" text @click="taxdialog = false">Close</v-btn>
                                  <v-btn color="success" text @click="newTaxCB()">Save</v-btn>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-col>
                            <v-file-input v-model="editedItem.img" accept="image/*" label="Product Image" show-size></v-file-input>
                        </v-row>
                      </v-container>
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="primary" text @click="close">Cancel</v-btn>
                      <v-btn color="success" text @click="inventorySave()">Save</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <v-dialog v-model="dialogDelete" max-width="500px">
                  <v-card>
                    <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                      <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-toolbar>
            </template>
            <template v-slot:[`item.calories`]="{ item }">
              <v-chip :color="getColor(item.calories)" dark>{{ item.calories }}</v-chip>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
              <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
            </template>
            <template v-slot:no-data>
              <v-btn color="primary" @click="initialize">Reset</v-btn>
            </template>
          </v-data-table>
        </template>
    </v-container>
  </v-app>
  </template>
<script>
  import SideBar from "../components/Sidebar";
  import TopBar from "../components/Topbar";
  import {db, storage} from "../firebase";
  import { getDocs, collection, addDoc} from "@firebase/firestore";
  import { ref, uploadBytesResumable, getDownloadURL } from "@firebase/storage"
  
  export default {
    components: {SideBar, TopBar},
    data: () => ({
      dialog: false,
      productdialog: false,
      branchdialog: false,
      taxdialog: false,
      dialogDelete: false,
      search: '',
      newProductName: null,
      newTax: null,
      newBranch: null,
      ProductNameId: null,
      imageUrl: '',
      products: [],
      branches: [],
      taxes: [],
      headers: [
        {
          text: 'ProductName',
          align: 'start',
          sortable: false,
          value: 'name',
        },
        { text: 'Branch', value: 'branch' },
        { text: 'Quantity', value: 'quantity' },
        { text: 'Alert Level', value: 'alert' },
        { text: 'Selling Price', value: 'price' },
        {text: 'Tax Rate (%)', value: 'tax' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        pname: null,
        branch: null,
        quantity: null,
        alert: null,
        price: null,
        tax: null,
        img: '',
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'ADD INVENTORY' : 'EDIT INVENTORY ITEM'
      },
    },

    watch: {
      //dialog (val) {
        //val || this.close()
      //},
      dialogDelete (val) {
        val || this.closeDelete()
      },
      dialog() {
        this.getProductname()
        this.getBranch()
        this.getTaxes()
      }
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.getInventory()
      },
      getColor (alert) {
        if (alert > 400) return 'red'
        else if (alert > 200) return 'orange'
        else return 'green'
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      async getProductname() {
        const querySnapshot = await getDocs(collection(db, "ProductName"))
        querySnapshot.forEach((doc) => {
          this.products.push(doc.data().productname)
        })
      },

      newProductCB() {
        addDoc(collection(db, "ProductName"), {productname: this.newProductName});
        alert("ProductName added successfully.");
        this.productdialog = false
        this.getProductname()
      },

      async getBranch() {
        const querySnapshot = await getDocs(collection(db, "Branch"))
        querySnapshot.forEach((doc) => {
          this.branches.push(doc.data().branch)
        })
      },

      newBranchCB() {
        addDoc(collection(db, "Branch"), {branch: this.newBranch})
        .then(() => {
          alert("Branch added successfully.");
          this.branchdialog = false
          this.getBranch()
        })
        .catch(function(error) {
          //Handle errors here
          
          alert(error);
        });
        
      },

      async getTaxes() {
        const querySnapshot = await getDocs(collection(db, "Tax"))
        querySnapshot.forEach((doc) => {
          this.taxes.push(doc.data().tax)
        })
      },

      newTaxCB() {
        addDoc(collection(db, "Tax"), {tax: this.newTax});
        alert("Tax rate added successfully.");
        this.taxdialog = false
        this.getTaxes()
      },

      async getInventory() {
        const querySnapshot = await getDocs(collection(db, "Inventory"))
        querySnapshot.forEach((doc) => {
          this.desserts.push(
            {
              name: doc.data().ProductName, 
              branch: doc.data().Branch,
              quantity: doc.data().Quantity, 
              alert: doc.data().Alert,
              price: doc.data().SellingPrice, 
              tax: doc.data().Tax
            }, 
          )
        })
      },

      async inventorySave() {
        const file = this.editedItem.img;
        // Upload file and metadata to the object 'images/mountains.jpg'
        const storageRef = ref(storage, 'products/' + file.name);
        const uploadTask = uploadBytesResumable(storageRef, file);

        // Listen for state changes, errors, and completion of the upload.
        uploadTask.on('state_changed',
           () => {
            // Upload completed successfully, now we can get the download URL
            getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
              console.log('File available at', downloadURL);
              addDoc(collection(db, "Inventory"), {ProductName: this.editedItem.pname, Branch: this.editedItem.branch, Quantity: this.editedItem.quantity, Alert: this.editedItem.alert, SellingPrice: this.editedItem.price, Tax: this.editedItem.tax, Image: downloadURL});
              alert("Saved successfully!");
            });
          }
        );
      },
    },
  }
</script>
  