<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background}">
    <SideBar/>
    <TopBar/>
    <v-container fluid >
      <template>
          <v-data-table :headers="headers" :items="desserts" :search="search" sort-by="name" class="elevation-1" >
            <template v-slot:top>
              <v-toolbar flat>
                    <v-menu v-model="fromDate" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field class="pt-6" v-model="dateFrom" label="From Date" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on"></v-text-field>
                      </template>
                      <v-date-picker v-model="dateFrom" @input="fromDate = false"></v-date-picker>
                    </v-menu>
                    <v-menu v-model="toDate" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field class="pt-6" v-model="dateTo" label="To Date" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on"></v-text-field>
                      </template>
                      <v-date-picker v-model="dateTo" @input="toDate = false"></v-date-picker>
                    </v-menu>
                  <v-btn @click="fetchData" outlined color="black" class="ml-4">Filter</v-btn>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialogDelete" max-width="500px">
                  <v-card>
                    <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                      <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-toolbar>
            </template>
            <template v-slot:[`item.status`]="{ item }">
              <v-chip :color="getColor(item.status)" dark>{{ item.status }}</v-chip>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
            </template>
            <template v-slot:no-data>
              <v-btn color="primary" @click="initialize">Reset</v-btn>
            </template>
          </v-data-table>
        </template>
    </v-container>
  </v-app>
  </template>
<script>
  import SideBar from "../components/Sidebar";
  import TopBar from "../components/Topbar";
  import {db} from "../firebase";
  import { getDocs, collection, query, orderBy, startAt, endAt } from "@firebase/firestore";
  import moment from 'moment';

  export default {
    components: {SideBar, TopBar},
    data: () => ({
      dialogDelete: false,
      search: '',
      statusCB: null,
      dateFrom: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      dateTo: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      fromDate: false,
      toDate: false,
      status: [],
      headers: [
        { text: 'Date', align: 'start', value: 'date', sortable: true },
        { text: 'Receipt No.', value: 'receipt', sortable: true },
        { text: 'ProductName', value: 'name', sortable: true },
        { text: 'Quantity', value: 'quantity' },
        { text: 'Unit Price', value: 'unitprice' },
        { text: 'Total Cost', value: 'total' },
        { text: 'VAT', value: 'vat' },
        { text: 'Payment Method', value: 'paymentmethod' },
        { text: 'Status', value: 'status' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
    }),

    computed: {
      
    },

    watch: {
      dialogDelete (val) {
        val || this.closeDelete()
      },
      from: 'fetchData',
      to: 'fetchData',
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.getCashRecords()
      },
      getColor (status) {
        if (status === 'Completed') return 'green'
        else return 'red'
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      async getCashRecords() {
        const querySnapshot = await getDocs(collection(db, "Sales"))
        querySnapshot.forEach((doc) => {
          
          this.desserts.push(
            {
              //date: new Date(doc.data().Date.seconds * 1000).toLocaleString(), 
              date: moment(doc.data().Date.toDate()).format('DD-MMM-YYYY, h:mm:ss a'), 
              receipt: doc.data().Receipt,
              name: doc.data().item['ProductName'], 
              quantity: doc.data().item['Qty'],
              unitprice: doc.data().item['UnitPrice'],
              total: doc.data().item['TotalCost'],
              vat: doc.data().item['VAT'],
              paymentmethod: doc.data().PaymentMethod,
              status: doc.data().Status
            }, 
          )
        })
      },

      
      // });
      async fetchData() {
          try {
             //const q = query(collection(db, "Sales"), where("Date", ">=", this.dateFrom), where("Date", "<=", this.dateTo), startAt(this.dateFrom), endAt(this.dateTo));
             const q = query(collection(db, "Sales"), orderBy("Date"), startAt(this.dateFrom), endAt(this.dateTo));

             const querySnapshot = await getDocs(q);
              this.desserts = [];
             querySnapshot.forEach((doc) => {
              console.log(doc.data());
                this.desserts.push(
                    {
                      //date: new Date(doc.data().Date.seconds * 1000).toLocaleString(), 
                      date: moment(doc.data().Date.toDate()).format('DD-MMM-YYYY, h:mm:ss a'), 
                      receipt: doc.data().Receipt,
                      name: doc.data().item['ProductName'], 
                      quantity: doc.data().item['Qty'],
                      unitprice: doc.data().item['UnitPrice'],
                      total: doc.data().item['TotalCost'],
                      vat: doc.data().item['VAT'],
                      paymentmethod: doc.data().PaymentMethod,
                      status: doc.data().Status
                    }, 
                  )
             });
          } catch (error) {
            console.log(error);
          }
  
      },
    },
  }
</script>
  