<script>
import {fb} from "../firebase"
import {signOut} from 'firebase/auth'
  export default {
    data: () => ({
      selectItem: 3,
      drawer: null,
      
        sales: [
          {
            title: "Cash Sale",
            icon: "mdi-account-multiple-outline",
            action: "cashsale"
          },
          {
            title: 'Invoice Sale', 
            icon: 'mdi-cog-outline',
            action: 'invoice'
          },
        ],
        salesrecords: [
          {
            title: "Cash Sales Records", 
            icon: 'mdi-plus-outline', 
            action: 'cashrecords'
          },
          {
            title: "Invoice Sales Records", 
            icon: 'mdi-plus-outline', 
            action: 'invoicerecords'
          },
        ],
        branch: [
          {
            title: "Branch Dispatch", 
            icon: 'mdi-plus-outline', 
            action: 'dispatch'
          },
          {
            title: "Branch Transaction", 
            icon: 'mdi-file-outline', 
            action: 'transaction'
          },
          {
            title: "Branch records", 
            icon: 'mdi-file-outline', 
            action: 'records'
          },
        ],
        
        }),
        methods: {
          
          logout() {
            //export declare function createUserWithEmailAndPassword(auth: Auth, email: string, password: string): Promise<UserCredential>;
              signOut(fb)
            .then((user) => {
              alert(user + " Logged Out Successful")
                this.$router.replace('/');
            })
                .catch(function(error) {
                    console.log(error);
                });
            
          },
          home() {
            this.$router.replace({name:'dashboard'})
          },
          branchdispatch() {
            this.$router.replace({name:'branchdispatch'})
          },
          branchrecords() {
            this.$router.replace({name:'branchrecords'})
          },
          finishedgoods() {
            this.$router.replace({name:'finishedgoods'})
          },
          rawmaterials() {
            this.$router.replace({name:'rawmaterials'})
          },
          rawmaterialconsumption() {
            this.$router.replace({name:'rawmaterialconsumption'})
          },
          workinprogress() {
            this.$router.replace({name:'workinprogress'})
          },
          customers() {
            this.$router.replace({name:'customers'})
          },
          suppliers() {
            this.$router.replace({name:'suppliers'})
          },
          purchaserecords() {
            this.$router.replace({name:'purchaserecords'})
          },
          purchaseorder() {
            this.$router.replace({name:'purchaseorder'})
          },
          overheads() {
            this.$router.replace({name:'overheadexpenses'})
          },
          generalexpenses() {
            this.$router.replace({name:'generalexpenses'})
          },
          salesActionClick(action) {
            if (action === "cashsale") {
              this.$router.replace({name:'saletransaction'})
            } else if (action === "invoice") {
              this.$router.replace({name:'invoicetransaction'})
            }
          },
          salesrecordsActionClick(action) {
            if (action === "cashrecords") {
              this.$router.replace({name:'salerecords'})
            } else if (action === "invoicerecords") {
              this.$router.replace({name:'invoicerecords'})
            }
          },
          branchActionClick(action) {
            if (action === "dispatch") {
              this.$router.replace({name:'branchdispatch'})
            } else if (action === "transaction") {
              this.$router.replace({name:'branchtransaction'})
            } else if (action === "records") {
              this.$router.replace({name:'branchrecords'})
            }
          }
  }
}
</script>
