<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background}">
    <SideBar/>
    <TopBar/>
    <v-container  fluid style="background-color: #323B50" >
      <v-card flat color="#323B50" class="my-2 card_global flex-grow-0">
        <v-list subheader two-line color="transparent">
          <v-list-title class="text-h5 white--text ">Bill To: </v-list-title>
            <v-list-item >
                  <v-col cols="12" md="2">
                      <v-select class="custom-select" v-model="salesrep" :items="repname" required label="Sales Rep"></v-select>
                  </v-col>
                  <v-col cols="2" >
                      <v-btn outlined @click="salesrepdialog = !salesrepdialog" color="white">ADD NEW</v-btn>
                      <v-dialog v-model="salesrepdialog" max-width="500px">
                          <v-card>
                          <v-card-title>
                              ENTER NEW SALES REP
                          </v-card-title>
                              <v-card-text>
                                  <v-text-field v-model="newSalesRep" required label="Names"></v-text-field>
                                  <v-text-field v-model="newPhoneNumber" required label="Phone Number"></v-text-field>
                              </v-card-text>
                          <v-card-actions>
                              <v-btn color="primary" text @click="salesrepdialog = false">Close</v-btn>
                              <v-btn color="success" text @click="newSalesRepCB()">Save</v-btn>
                          </v-card-actions>
                          </v-card>
                      </v-dialog>
                  </v-col>
                  <v-col cols="12" md="2">
                      <v-select class="custom-select" v-model="plates" :items="platenumber" required label="Truck Number Plate"></v-select>
                  </v-col>
                  <v-col cols="2" >
                      <v-btn outlined @click="platesdialog = !platesdialog" color="white">ADD NEW</v-btn>
                      <v-dialog v-model="platesdialog" max-width="500px">
                          <v-card>
                          <v-card-title>
                              ADD NEW TRUCK NUMBER PLATES
                          </v-card-title>
                              <v-card-text>
                                  <v-text-field v-model="newTruckPlate" required label="Truck Plate Number"></v-text-field>
                              </v-card-text>
                          <v-card-actions>
                              <v-btn color="primary" text @click="platesdialog = false">Close</v-btn>
                              <v-btn color="success" text @click="newTruckPlateCB()">Save</v-btn>
                          </v-card-actions>
                          </v-card>
                      </v-dialog>
                  </v-col>
                  <v-col cols="12" md="2">
                      <v-select class="custom-select" v-model="branch" :items="branchName" required label="Branch Name"></v-select>
                  </v-col>
                  <v-col cols="2" >
                      <v-btn outlined @click="branchdialog = !branchdialog" color="white">ADD NEW</v-btn>
                      <v-dialog v-model="branchdialog" max-width="500px">
                          <v-card>
                          <v-card-title>
                              ADD NEW BRANCH
                          </v-card-title>
                              <v-card-text>
                                  <v-text-field v-model="newBranch" required label="Branch Name"></v-text-field>
                              </v-card-text>
                          <v-card-actions>
                              <v-btn color="primary" text @click="branchdialog = false">Close</v-btn>
                              <v-btn color="success" text @click="newBranchCB()">Save</v-btn>
                          </v-card-actions>
                          </v-card>
                      </v-dialog>
                  </v-col>
              </v-list-item>
          </v-list>
        <!-- <h2 class="mt-2" >Branch Dispatch Transaction</h2> -->
        <!--p class="pl-4">No Credit transactions.</p-->
        <v-row >
          <v-col cols="12" sm="8" class="pa-2">
            <!-- Show progress bar while products are loading -->
            <v-progress-linear v-if="!productsLoaded" indeterminate class="mb-4"></v-progress-linear>
            <!-- Show product list when products are loaded -->
            <!-- <v-card flat class="card_item pa-2" color="#323B50"> -->
              <v-list v-else color="#323B50" style="height: 570px; overflow-y: scroll; overflow-x: hidden">
              <v-row>
                  <template v-if="$vuetify.breakpoint.smAndUp">
                    <v-col v-for="(product, i) in productInventory"
                      :key="i"
                      cols="12"
                      sm="8"
                      md="6"
                      lg="4"
                      xl="3">
                      <v-hover v-slot="{hover}" close-delay="200">
                        <v-card :elevation="hover ? 16 :2" :class="{ 'on-hover' : hover}"
                        class="mx-auto align-center" height="250" tile color="#333c51" @click="productSelect(product)">
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn tile small :color="hover ? '#F34F64' : '#777D8B'" dark>
                              {{product.SellingPrice}}
                            </v-btn>
                          </v-card-actions>
                          <v-img :src="product.Image" width="150" height="150"></v-img>
                          <v-card-text :class="hover ? 'red--text' : 'white--text'">
                            {{product.ProductName}}
                          </v-card-text>
                        </v-card>
                      </v-hover>
                    </v-col>
                  </template>
                  <template v-if="$vuetify.breakpoint.smAndDown">
                    <v-col v-for="(product, i) in productInventory"
                    :key="i"
                    cols="12"
                    sm="6"
                    md="4"
                    lg="3"
                    xl="2">
                    <v-hover v-slot="{hover}" close-delay="200">
                      <v-card :elevation="hover ? 16 :2" :class="{ 'on-hover' : hover}"
                      class="mx-auto align-center" height="250" tile color="#333c51" @click="productSelect(product)">
                      <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn tile small :color="hover ? '#F34F64' : '#777D8B'" dark>
                            {{product.SellingPrice}}
                          </v-btn>
                        </v-card-actions>
                        <v-img :src="product.Image" width="150" height="150"></v-img>
                        <v-card-text :class="hover ? 'red--text' : 'white--text'">
                          {{product.ProductName}}
                        </v-card-text>
                      </v-card>
                    </v-hover>
                  </v-col>
                  </template>
              </v-row>
            </v-list>
            <!-- </v-card> -->
          </v-col>
          <v-col cols="12" sm="4" v-if="$vuetify.breakpoint.smAndUp">
            <dispatchCart/>
          </v-col>
        </v-row>
        <v-btn
          v-if="$vuetify.breakpoint.smAndDown"
          icon
          @click="showCart = !showCart"
        >
          <v-icon color="white">mdi-cart</v-icon>
        </v-btn>
        <v-card v-if="showCart" class="my-2" flat color="#323B50">
          <dispatchCart />
        </v-card>
      </v-card>
    </v-container>
  </v-app>
</template>

<script>
import dispatchCart from "../components/DispatchCart";
import SideBar from "../components/Sidebar";
import TopBar from "../components/Topbar";
import {db} from "../firebase";
import { getDocs, collection, addDoc} from "@firebase/firestore";

  export default {
    components: {dispatchCart, SideBar, TopBar},
    data: () => ({
      max25chars: (string) => string.substring(0, 25),
      showCart: false,
      productsLoaded: false,
      salesrepdialog: false,
      platesdialog: false,
      branchdialog: false,
      snack: false,
      snackColor: '',
      snackText: '',
      salesrep: null,
      plates: null,
      branch: null,
      newSalesRep: null,
      newPhoneNumber: null,
      newTruckPlate: null,
      newBranch: null,
      repname: [],
      platenumber: [],
      branchName: []
    }),
    computed: {
      cart: state => state.dispatchCart,
      productInventory() {
        return this.$store.getters.productInventory;
      },
    },
    created () {
      this.loadProducts();
      this.$store.dispatch("getInventory");
      this.getSalesRep();
      this.getTruckPlates();
      this.getBranch()

    },
    methods: {
      loadProducts() {
        // code to load products goes here
        this.productsLoaded = true;
      },

      async getSalesRep() {
          const querySnapshot = await getDocs(collection(db, "SalesRep"))
          querySnapshot.forEach((doc) => {
          this.repname.push(doc.data().Names)
          })
      },

      newSalesRepCB() {
          addDoc(collection(db, "SalesRep"), {Names: this.newSalesRep, PhoneNumber: this.newPhoneNumber});
          alert("Sales Rep added successfully.");
          this.salesrepdialog = false
          this.getSalesRep()
      },

      async getTruckPlates() {
          const querySnapshot = await getDocs(collection(db, "TruckPlates"))
          querySnapshot.forEach((doc) => {
          this.platenumber.push(doc.data().Plates)
          })
      },

      newTruckPlateCB() {
          addDoc(collection(db, "TruckPlates"), {Plates: this.newTruckPlate});
          alert("Truck Plates added successfully.");
          this.platesdialog = false
          this.getTruckPlates()
      },

      async getBranch() {
        const querySnapshot = await getDocs(collection(db, "Branch"))
        querySnapshot.forEach((doc) => {
          this.branchName.push(doc.data().branch)
        })
      },

      newBranchCB() {
        addDoc(collection(db, "Branch"), {branch: this.newBranch})
        .then(() => {
          alert("Branch added successfully.");
          this.branchdialog = false
          this.getBranch()
        })
        .catch(function(error) {
          //Handle errors here
          
          alert(error);
        });
        
      },

      async productSelect(product) {
        // Find the product in the cart
        const cartProduct = this.$store.state.dispatchCart.find(p => p.ProductName === product.ProductName);
        // Find the product in the productInventory array
        const inventoryProduct = this.productInventory.find(p => p.ProductName === product.ProductName);
        if (cartProduct) {
          if (inventoryProduct) {
            // If the product exists in the cart, increment the Qty
            cartProduct.Qty++;
            cartProduct.TotalCost = cartProduct.UnitPrice * cartProduct.Qty;
            cartProduct.VAT = cartProduct.TotalCost * inventoryProduct.Tax/100;

            // Commit the pushProductToCart mutation to update the cart state
            this.$store.commit('pushProductToDispatchCart', cartProduct);
          } else {
            // If the product exists in the cart, increment the Qty
            cartProduct.Qty++;
            cartProduct.TotalCost = cartProduct.UnitPrice * cartProduct.Qty;
            cartProduct.VAT = cartProduct.TotalCost * 0;

            // Commit the pushProductToCart mutation to update the cart state
            this.$store.commit('pushProductToDispatchCart', cartProduct);
          }
          
        } else {
          if (inventoryProduct) {
              this.$store.commit('pushProductToDispatchCart', {
                ProductName: product.ProductName,
                UnitPrice: product.SellingPrice,
                Qty: 1,
                TotalCost: product.SellingPrice * 1,
                VAT: product.SellingPrice * inventoryProduct.Tax/100
              });
          } else {
            // If the product does not exist in the cart, add a new object to the cart
            this.$store.commit('pushProductToDispatchCart', {
              ProductName: product.ProductName,
              UnitPrice: product.SellingPrice,
              Qty: 1,
              TotalCost: product.SellingPrice * 1,
              VAT: product.SellingPrice * 0
            });
          }
        }
      }
    },
  }
</script>
<style>
  /* width */
  ::-webkit-scrollbar {
    width: 10px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #fff;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #555;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  h2 {
    color: #fff !important;
    cursor: pointer !important;
  }
  p{
    color: #fff;
    cursor: pointer;
  }
  .custom-select .v-select__selections {
    color: white !important;
  }

  .custom-select .v-select__label {
    color: white !important;
  }
</style>