<template>
    <v-card flat class="card_item pa-2 mt-2" elevation="2">
      <!--v-list subheader two-line color="transparent">
        <v-list-item >
          <v-list-item-avatar size="50px">
            <v-icon class="blue-grey darken-2" color="white" dark >mdi-warehouse</v-icon>
          </v-list-item-avatar>
          <v-list-item-content >
            <v-list-item-title class="white--text"></v-list-item-title>
            <v-list-item-subtitle class="grey--text mt-2"><v-combobox class="pt-2" v-model="Branches" :items="branch" label="Branch Name" clearable  dense></v-combobox></v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list-->
      <v-data-table :headers="headers" :items="products" sort-by="calories" class="elevation-1">
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>Cart Items</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                  <v-btn color="success" text @click="deleteItemConfirm">OK</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.units`]="props">
          <v-edit-dialog :return-value.sync="props.item.units" large persistent @save="saveUnits" @cancel="cancelUnits" @open="openUnits" @close="closeUnits">
            <div>{{ props.item.units }}</div>
            <template v-slot:input>
              <div class="mt-4 text-h6">Update Units</div>
              <v-text-field v-model="props.item.units" :rules="[max25chars]" label="Edit" single-line counter autofocus></v-text-field>
            </template>
          </v-edit-dialog>
        </template>
        <template v-slot:[`item.add`]="{ item }">
          <v-btn @click="editItem(item)" text icon color="black lighten-2">
            <v-icon small color="red">mdi-minus</v-icon>
          </v-btn>
        </template>
        <template v-slot:[`item.minus`]="{ item }">
          <v-btn @click="deleteItem(item)"  text icon color="black lighten-2">
            <v-icon small color="red">mdi-plus</v-icon>
          </v-btn>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-btn @click="deleteItem(item)"  text icon color="black lighten-2">
            <v-icon small color="red">mdi-delete</v-icon>
          </v-btn>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="initialize">
            Reset
          </v-btn>
        </template>
      </v-data-table>
      <v-snackbar v-model="snack" :timeout="3000" :color="snackColor">
        {{ snackText }}
        <template v-slot:action="{ attrs }">
          <v-btn v-bind="attrs" text @click="snack = false">Close</v-btn>
        </template>
      </v-snackbar>
      <v-list>
        <v-footer padless>
          <v-col class="text-center" cols="12">
            <strong>Raw Material Value: </strong>
            <strong>Ksh. 20,000</strong>
          </v-col>
        </v-footer>
        <!--v-list-item>
          <v-list-item-content>
            <v-list-item-subtitle class="black--text" >VAT</v-list-item-subtitle>
            <v-list-item-title class="black--text subtitle-2 mt-2">TOTAL COST</v-list-item-title>
          </v-list-item-content>
          <v-list-item-action>
            <v-list-item-subtitle class="orange--text">Ksh.50</v-list-item-subtitle>
            <v-list-item-title class="green--text subtitle-2 mt-2">Ksh.200</v-list-item-title>
          </v-list-item-action>
        </v-list-item-->
        <v-divider color="#3F495E" class="divider_tab"></v-divider>
        <template>
          <v-row justify="center">
              <v-dialog v-model="dialog" width="600px">
                    <template v-slot:activator="{ on, attrs }" >
                      <!--div v-slot:activator="{ on, attrs }" class="text-right mt-6 mb-6"-->
                        <v-btn color="#F34F64" dark class="withoutuppercase text-right " v-bind="attrs" v-on="on">CheckOut</v-btn>
                      <!--/div-->
                      <!--v-btn color="primary" dark v-bind="attrs" v-on="on">Open Dialog</v-btn-->
                    </template>
                      <v-card class="pa-2">
                        <v-card-title>
                          <span class="text-h5">Cart CheckOut</span>
                        </v-card-title>
                        <v-data-table :headers="headers" :items="products" sort-by="calories" class="elevation-1">
                            <template v-slot:top>
                              <v-toolbar flat>
                                <v-toolbar-title>Cart Items</v-toolbar-title>
                                <v-spacer></v-spacer>
                                <v-dialog v-model="dialogDelete" max-width="500px">
                                  <v-card>
                                    <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                                    <v-card-actions>
                                      <v-spacer></v-spacer>
                                      <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                                      <v-btn color="success" text @click="deleteItemConfirm">OK</v-btn>
                                      <v-spacer></v-spacer>
                                    </v-card-actions>
                                  </v-card>
                                </v-dialog>
                              </v-toolbar>
                            </template>
                            <template v-slot:[`item.units`]="props">
                              <v-edit-dialog :return-value.sync="props.item.units" large persistent @save="saveUnits" @cancel="cancelUnits" @open="openUnits" @close="closeUnits">
                                <div>{{ props.item.units }}</div>
                                <template v-slot:input>
                                  <div class="mt-4 text-h6">Update Units</div>
                                  <v-text-field v-model="props.item.units" :rules="[max25chars]" label="Edit" single-line counter autofocus></v-text-field>
                                </template>
                              </v-edit-dialog>
                            </template>
                            <template v-slot:[`item.add`]="{ item }">
                              <v-btn @click="editItem(item)" text icon color="black lighten-2">
                                <v-icon small color="red">mdi-minus</v-icon>
                              </v-btn>
                            </template>
                            <template v-slot:[`item.minus`]="{ item }">
                              <v-btn @click="deleteItem(item)"  text icon color="black lighten-2">
                                <v-icon small color="red">mdi-plus</v-icon>
                              </v-btn>
                            </template>
                            <template v-slot:[`item.actions`]="{ item }">
                              <v-btn @click="deleteItem(item)"  text icon color="black lighten-2">
                                <v-icon small color="red">mdi-delete</v-icon>
                              </v-btn>
                            </template>
                            <template v-slot:no-data>
                              <v-btn color="primary" @click="initialize">
                                Reset
                              </v-btn>
                            </template>
                          </v-data-table>
                          <v-list>
                            <v-footer padless>
                              <v-list-item class="pl-12 pr-12">
                                <v-list-item-content>
                                  <!--v-list-item-subtitle class="black--text" >VAT</v-list-item-subtitle-->
                                  <v-list-item-title class="black--text subtitle-2 mt-2">Raw Material Value:</v-list-item-title>
                                </v-list-item-content>
                                <v-list-item-action>
                                  <!--v-list-item-subtitle class="orange--text">Ksh.50</v-list-item-subtitle-->
                                  <v-list-item-title class="green--text subtitle-2 mt-2">Ksh.200</v-list-item-title>
                                </v-list-item-action>
                              </v-list-item>
                            </v-footer>
                          </v-list>
                      <v-card-actions justify="space-around">
                        <v-spacer></v-spacer>
                        <v-btn text outlined color="success" @click="dialog = false">Transfer To Production</v-btn>
                      </v-card-actions>
                  </v-card>
                </v-dialog>
            </v-row>
          </template>
        <!--div class="text-right mt-6 mb-6">
          <v-btn color="#F34F64" dark rounded class="withoutuppercase">CheckOut</v-btn>
        </div-->
      </v-list>
    </v-card>
  </template>
  <script>
    export default {
      data: () => ({
        drawer: true,
        dialog: false,
        snack: false,
        snackColor: '',
        snackText: '',
        dialogDelete: false,
        headers: [
              {
                text: 'Product Name',
                align: 'start',
                sortable: false,
                value: 'name',
              },
              { text: 'Price/Unit', value: 'unitPrice' },
              { text: '', value: 'add', sortable: false },
              { text: 'Units', value: 'units' },
              { text: '', value: 'minus', sortable: false },
              { text: 'Total Cost', value: 'totalcost' },
              { text: 'Delete', value: 'actions', sortable: false },
              
            ],
            products: [],
            defaultItem: {
              name: '',
              unitPrice: 0,
              units: 0,
              totalcost: 0,
            },
      }),
      computed: {
            formTitle () {
              return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
            },
          },
      
          watch: {
            dialog (val) {
              val || this.close()
            },
            dialogDelete (val) {
              val || this.closeDelete()
            },
          },
      
          created () {
            this.initialize()
          },
      
          methods: {
            decrement() {
              this.bpm--
            },
            increment () {
              this.bpm++
            },
            initialize () {
              this.products = [
                {
                  name: '500ml passion juice',
                  unitPrice: 250.00,
                  units: 10,
                  totalcost: 2500.00,
                },
                {
                  name: '500ml mango juice',
                  unitPrice: 250.00,
                  units: 20,
                  totalcost: 5000.00,
                },
                {
                  name: '1Litre passion juice',
                  unitPrice: 500.00,
                  units: 10,
                  totalcost: 5000.00,
                },
                {
                  name: '1Litre pineapple juice',
                  unitPrice: 500.00,
                  units: 20,
                  totalcost: 5000.00,
                },
              ]
            },
      
            editItem (item) {
              this.editedIndex = this.products.indexOf(item)
              this.editedItem = Object.assign({}, item)
              this.dialog = true
            },
      
            deleteItem (item) {
              this.editedIndex = this.products.indexOf(item)
              this.editedItem = Object.assign({}, item)
              this.dialogDelete = true
            },
      
            deleteItemConfirm () {
              this.products.splice(this.editedIndex, 1)
              this.closeDelete()
            },
      
            close () {
              this.dialog = false
              this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
              })
            },
      
            closeDelete () {
              this.dialogDelete = false
              this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
              })
            },
      
            save () {
              if (this.editedIndex > -1) {
                Object.assign(this.products[this.editedIndex], this.editedItem)
              } else {
                this.products.push(this.editedItem)
              }
              this.close()
            },
            saveUnits () {
                this.snack = true
                this.snackColor = 'success'
                this.snackText = 'Data saved'
            },
            cancelUnits () {
                this.snack = true
                this.snackColor = 'error'
                this.snackText = 'Canceled'
            },
            openUnits () {
              this.snack = true
              this.snackColor = 'info'
              this.snackText = 'Dialog opened'
            },
            closeUnits () {
              console.log('Dialog closed')
            },
          },
    }
  </script>