<template>
  <v-card flat class="card_item pa-2 mt-2" elevation="2">
      <v-list subheader two-line color="transparent">
      </v-list>
      <v-data-table :headers="headers" :items="selectedProducts" class="elevation-1">
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title>Cart Items</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-dialog v-model="dialogDelete" max-width="500px">
                <v-card>
                  <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                    <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:[`item.ProductName`]="{ item }">
            {{ item.ProductName }}
          </template>
          <template v-slot:[`item.UnitPrice`]="{ item }">
            {{ item.UnitPrice }}
          </template>
          <template v-slot:[`item.Qty`]="{ item }">
            <v-edit-dialog :return-value.sync="item.Qty" large persistent @save="saveUnits" @cancel="cancelUnits" @open="openUnits" @close="closeUnits">
              <div>{{ item.Qty }}</div>
              <template v-slot:input>
                <div class="mt-4 text-h6">Update Units</div>
                <v-text-field v-model="item.Qty" label="Edit" single-line counter autofocus></v-text-field>
              </template>
            </v-edit-dialog>
          </template>
          <template v-slot:[`item.TotalCost`]="{ item }">
            {{ item.TotalCost }}
          </template>
          <template v-slot:[`item.VAT`]="{ item }">
            {{ item.VAT }}
          </template>
          <template v-slot:[`item.minus`]="{ item }">
            <v-btn @click="decrementQuantity(item)" text icon color="black lighten-2">
              <v-icon small color="red">mdi-minus</v-icon>
            </v-btn>
          </template>
          <template v-slot:[`item.add`]="{ item }">
            <v-btn @click="incrementQuantity(item)"  text icon color="black lighten-2">
              <v-icon small color="red">mdi-plus</v-icon>
            </v-btn>
          </template>
          <template v-slot:[`item.actions`]="{ item }">
            <v-btn @click="deleteItem(item)"  text icon color="black lighten-2">
              <v-icon small color="red">mdi-delete</v-icon>
            </v-btn>
          </template>
          <template v-slot:no-data>
            <v-btn color="primary" @click="initialize">
              Reset
            </v-btn>
          </template>
      </v-data-table>
      <v-list>
            <v-footer padless>
              <v-col class="text-center" cols="12">
                <strong>Grand Total: </strong>
                <strong>Ksh. {{ totalCost }}</strong>
              </v-col>
            </v-footer>
            <v-divider color="#3F495E" class="divider_tab"></v-divider>
            <template>
              <v-row justify="center">
                  <v-dialog v-model="dialog" width="600px">
                        <template v-slot:activator="{ on, attrs }" >
                            <v-btn color="#F34F64" dark class="withoutuppercase text-right " v-bind="attrs" v-on="on">CheckOut</v-btn>
                        </template>
                          <v-card class="pa-2">
                            <v-card-title>
                              <span class="text-h5">Cart CheckOut</span>
                            </v-card-title>
                            <v-data-table :headers="headers" :items="selectedProducts" class="elevation-1">
                              <template v-slot:top>
                                  <v-toolbar flat>
                                    <!-- <v-toolbar-title>Cart Items</v-toolbar-title> -->
                                    <v-spacer></v-spacer>
                                    <v-dialog v-model="dialogDelete" max-width="500px">
                                      <v-card>
                                        <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                                        <v-card-actions>
                                          <v-spacer></v-spacer>
                                          <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                                          <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                                          <v-spacer></v-spacer>
                                        </v-card-actions>
                                      </v-card>
                                    </v-dialog>
                                  </v-toolbar>
                                </template>
                                <template v-slot:[`item.ProductName`]="{ item }">
                                  {{ item.ProductName }}
                                </template>
                                <template v-slot:[`item.UnitPrice`]="{ item }">
                                  {{ item.UnitPrice }}
                                </template>
                                <template v-slot:[`item.Qty`]="{ item }">
                                  {{ item.Qty }}
                                  <v-edit-dialog :return-value.sync="item.Qty" large persistent @save="saveUnits" @cancel="cancelUnits" @open="openUnits" @close="closeUnits">
                                    <template v-slot:input>
                                      <div class="mt-4 text-h6">Update Units</div>
                                      <v-text-field v-model="item.Qty" :rules="[max25chars]" label="Edit" single-line counter autofocus></v-text-field>
                                    </template>
                                  </v-edit-dialog>
                                </template>
                                <template v-slot:[`item.TotalCost`]="{ item }">
                                  {{ item.TotalCost }}
                                </template>
                                <template v-slot:[`item.VAT`]="{ item }">
                                  {{ item.VAT }}
                                </template>
                                <template v-slot:[`item.minus`]="{ item }">
                                  <v-btn @click="decrementQuantity(item)" text icon color="black lighten-2">
                                    <v-icon small color="red">mdi-minus</v-icon>
                                  </v-btn>
                                </template>
                                <template v-slot:[`item.add`]="{ item }">
                                  <v-btn @click="incrementQuantity(item)"  text icon color="black lighten-2">
                                    <v-icon small color="red">mdi-plus</v-icon>
                                  </v-btn>
                                </template>
                                <template v-slot:[`item.actions`]="{ item }">
                                  <v-btn @click="deleteItem(item)"  text icon color="black lighten-2">
                                    <v-icon small color="red">mdi-delete</v-icon>
                                  </v-btn>
                                </template>
                                <template v-slot:no-data>
                                  <v-btn color="primary" @click="initialize">
                                    Reset
                                  </v-btn>
                                </template>
                              </v-data-table>
                              <v-list>
                                <v-footer padless>
                                  <v-col class="text-center" cols="12">
                                    <strong>Grand Total: </strong>
                                    <strong>Ksh. {{ totalCost }}</strong>
                                  </v-col>
                                </v-footer>
                              </v-list>
                          <v-card-actions justify="space-around">
                            <v-spacer></v-spacer>
                            <v-btn text color="primary" @click="cashDialog = true">CASH</v-btn>
                            <v-btn text color="success" @click="mpesaDialog = true">MPESA</v-btn>
                            <v-btn text color="primary" @click="dialog = false">CARD</v-btn>
                            <v-btn text color="error" @click="saveAsDraft">SAVE AS DRAFT</v-btn>
                          </v-card-actions>
                      </v-card>
                    </v-dialog>
                    <v-dialog v-model="cashDialog" max-width="500px" persistent>
                      <v-card>
                        <!-- Dialog content goes here -->
                        <v-card-text>
                          <v-layout row wrap>
                            <v-card flat height="350"  class="mx-auto align-center">
                              <!-- Other card content here -->
                              <v-img :src="require('@/assets/images/cashpay.png')" ></v-img>
                            </v-card>
                            <v-col class=" h5 text-center" cols="12">
                              <strong>Grand Total: </strong>
                              <strong>Ksh. {{ totalCost }}</strong>
                            </v-col>
                            <v-flex xs12>
                              <v-text-field v-model="cashPaid" label="Cash Paid" class="ma-4" ></v-text-field>
                            </v-flex>
                          </v-layout>
                        </v-card-text>
                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn color="blue darken-1" text  @click="cashDialog = false">Close</v-btn>
                          <v-btn color="primary" text @click="submitForm">Save</v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                    <v-snackbar v-model="snack" :timeout="5000" :color="snackColor">
                      {{ snackText }}
                      <template v-slot:action="{ attrs }">
                        <v-btn v-bind="attrs" text @click="snack = false">Close</v-btn>
                      </template>
                    </v-snackbar>
                    <v-dialog v-model="mpesaDialog" max-width="500px">
                      <v-card>
                        <!-- Dialog content goes here -->
                        <v-card-text>
                          <v-layout row wrap>
                            <v-card flat class="mx-auto align-center">
                              <!-- Other card content here -->
                              <v-img class="mx-auto align-center" width="200" :src="require('@/assets/images/mobile-money.png')" ></v-img>
                            </v-card>
                            <v-col class=" h5 text-center" cols="12">
                              <strong>Grand Total: </strong>
                              <strong>Ksh. {{ totalCost }}</strong>
                            </v-col>
                            <v-flex xs12>
                              <v-text-field v-model="clientNumber" label="Client Phone Number" class="ma-4" Number></v-text-field>
                            </v-flex>
                          </v-layout>
                        </v-card-text>
                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn color="blue darken-1" text  @click="mpesaDialog = false">Close</v-btn>
                          <v-btn color="primary" text @click="mpesaStkPush">Send Notification</v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                    <v-dialog v-model="receiptDialog" max-width="600">
                      <v-card>
                        <v-card-title class="headline">Receipt</v-card-title>
                        <v-card-text>
                        </v-card-text>
                        <v-card-actions>
                          <v-btn @click="receiptDialog = false" color="primary">Close</v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                </v-row>
              </template>
      </v-list>
  </v-card>
</template>
<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
  export default {
    props: ['cartData', 'max25chars'],
    name: "salesCart",
    data: () => ({
      drawer: true,
      dialog: false,
      snack: false,
      snackColor: '',
      snackText: '',
      dialogDelete: false,
      cashDialog: false,
      mpesaDialog: false,
      receiptDialog: false,
      paid: null,
      clientNumber: null,
      cashPaid: 0,
      Branches: null,
      branch: [
          'Junction',
          'Carrefour MSA RD',
          'TRM',
          'Sarit Center',
        ],
        headers: [
            { text: 'Product Name', align: 'start', sortable: true, value: 'ProductName' },
            { text: 'Unit Price', value: 'UnitPrice' },
            { text: '', value: 'minus', sortable: false },
            { text: 'Quantity', value: 'Qty' },
            { text: '', value: 'add', sortable: false },
            { text: 'Total Cost', value: 'TotalCost' },
            { text: 'VAT', value: 'VAT' },
            { text: 'Delete', value: 'actions', sortable: false },
          ],
        
          defaultItem: {  
            name: '',
            unitPrice: 0,
            units: 0,
            totalcost: 0,
          },
    }),
    computed: {
      productInventory() {
        return this.$store.getters.productInventory;
      },
      totalCost() {
        return this.$store.getters.totalCost;
      },
       ...mapGetters(['cart']),
      selectedProducts() {
         return this.$store.getters.cart;
      },
      cart: {
        get() {
          return this.cartData;
        },
        set(cart) {
          this.$emit('update:cartData', cart);
        },
      },
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },
    created () {
      this.initialize()
    },
      
    methods: {
      ...mapActions(['saveDraftToFirestore', 'saveCartToFirestore']),
     
      
      
      async mpesaStkPush() {
        if (!navigator.onLine) {
            // Show an error message or a notification to inform the user that they are not connected to the internet
            // alert("You are offline, please connect to the internet before proceeding")
            this.snackColor = 'error'
            this.snackText = 'You are offline, please connect to the internet.'
            this.snack = true
            return;
        }
        try {
          // Get the client phone number from the textfield
          const phoneNumber = this.clientNumber;

          let headers = new Headers();
          headers.append("Content-Type", "application/json");
          headers.append("Authorization", "Bearer XjlINa1yTJOGbKi0O7Gcr825nW4t");

          fetch("https://sandbox.safaricom.co.ke/mpesa/stkpush/v1/processrequest", {
            method: 'POST',
            headers,
            body: JSON.stringify({
              "BusinessShortCode": 174379,
              "Password": "MTc0Mzc5YmZiMjc5ZjlhYTliZGJjZjE1OGU5N2RkNzFhNDY3Y2QyZTBjODkzMDU5YjEwZjc4ZTZiNzJhZGExZWQyYzkxOTIwMjMwMTA5MTExMDIz",
              "Timestamp": new Date(),
              "TransactionType": "CustomerPayBillOnline",
              "Amount": 1,
              "PartyA": 254724396084,
              "PartyB": 174379,
              "PhoneNumber": phoneNumber,
              "CallBackURL": "https://mydomain.com/path",
              "AccountReference": "CompanyXLTD",
              "TransactionDesc": "Payment of X" 
            })
          })
            .then(response => response.text())
            .then(result => console.log(result))
            .catch(error => console.log(error));
        } catch (error) {
          this.snackColor = 'error'
          this.snackText = error
          this.snack = true
        }
      },

      incrementQuantity(item) {
        // Find the product in the cart
        const cartProduct = this.$store.state.cart.find(p => p.ProductName === item.ProductName);
        // Find the product in the productInventory array
        const inventoryProduct = this.productInventory.find(p => p.ProductName === item.ProductName);
        if (cartProduct) {
          if (inventoryProduct) {
            // Check if the number of units in the cart are equal to number of units in stock
            if (cartProduct.Qty === Number(inventoryProduct.Quantity)){
              
              // Display an error message if the number of units in the cart match those that are in stock
              this.snackColor = 'error'
              this.snackText = 'Error: Maximum number of units in stock reached. '
              this.snack = true
              return;
            }else{
              
              // If the number of units in the cart are less than units in stock then proceed increment units by one
              item.Qty++;
              item.TotalCost = item.UnitPrice * item.Qty;
              // Dispatch a Vuex action to update the quantity of the item in the store
              this.$store.dispatch('updateQuantity', { item, quantity: item.Qty, totalcost: item.TotalCost });
            }
          }
        }
      },

      decrementQuantity(item) {
        if (item.Qty > 1) {
          item.Qty--;
          item.TotalCost = item.UnitPrice * item.Qty;
          // Dispatch a Vuex action to update the quantity of the item in the store
          this.$store.dispatch('updateQuantity', { item, quantity: item.Qty, totalcost: item.TotalCost });
        } else {
          this.snackColor = 'error'
          this.snackText = 'Unable to reduce units below zero. Please delete item from cart.'
          this.snack = true
        }
      },

      async submitForm() {
        if (!navigator.onLine) {
            // Show an error message or a notification to inform the user that they are not connected to the internet
            // alert("You are offline, please connect to the internet before proceeding")
            this.snackColor = 'error'
            this.snackText = 'You are offline, please connect to the internet.'
            this.snack = true
            return;
        }
        try {
          if (this.cashPaid < this.totalCost) {
            // Display an error message if the cash paid is less than the total cost
            this.snackColor = 'error'
            this.snackText = 'Error: Not enough cash paid'
            this.snack = true
            return;
          } else if (this.cashPaid === '') {
            this.snackColor = 'error'
            this.snackText = 'Please enter the Cash Paid'
            this.snack = true
            return;
          } else {
            await this.saveCartToFirestore(); // Save the cart data to the Firestore

            // Calculate the change due
            const changeDue = this.cashPaid - this.totalCost;
            this.snack = true
            this.snackColor = 'success'
            this.snackText = `Saved Successfully. Change due: ${changeDue}`

            // Reset the form and close the dialog
            this.cashPaid = 0;
            //this.cashDialog = false;
          }
        } catch (error) {
          this.snackColor = 'error'
          this.snackText = error
          this.snack = true
        }
      },

      async saveAsDraft() {
        if (!navigator.onLine) {
            // Show an error message or a notification to inform the user that they are not connected to the internet
            // alert("You are offline, please connect to the internet before proceeding")
            this.snackColor = 'error'
            this.snackText = 'You are offline, please connect to the internet.'
            this.snack = true
            return;
        }
        try {
          await this.saveDraftToFirestore(); // Save the cart data to the Firestore as draft

          this.snack = true
          this.snackColor = 'success'
          this.snackText = `Draft Saved Successfully.`
        } catch (error) {
          this.snack = true
          this.snackColor = 'error'
          this.snackText = error
        }
      },

      initialize () {
        this.products = []
      },

      editItem (item) {
        this.editedIndex = this.products.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        item.selected = true

        // Find the index of the item in the local selectedProducts array
        // const index = this.selectedProducts.findIndex(product => product.ProductName === item.ProductName)

        // Dispatch a Vuex action to remove the item from the cart array in the store
        this.$store.dispatch('removeItem', item)
        // item.selected = true
        // this.editedIndex = this.selectedProducts.indexOf(item)
        // this.editedItem = Object.assign({}, item)
        // this.dialogDelete = true
      },

      deleteItemConfirm (item) {
        // Find the index of the item in the local selectedProducts array
        //const index = this.selectedProducts.findIndex(product => product.ProductName === item.ProductName)

        // Dispatch a Vuex action to remove the item from the cart array in the store
        this.$store.dispatch('removeItem', item)

        // Remove the item from the selectedProducts array
        // this.selectedProducts.splice(index, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.products[this.editedIndex], this.editedItem)
        } else {
          this.products.push(this.editedItem)
        }
        this.close()
      },
      saveUnits(item) {
        // Dispatch a Vuex action to update the quantity of the item in the store
          this.$store.dispatch('updateQuantity', { item, quantity: item.Qty });
          this.snack = true
          this.snackColor = 'success'
          this.snackText = 'Data saved'
      },
      cancelUnits (item) {
        // Reset the item's quantity to the original value
          item.Qty = this.originalQuantity;
          this.snack = true
          this.snackColor = 'error'
          this.snackText = 'Canceled'
      },
      openUnits () {
        this.snack = true
        this.snackColor = 'info'
        this.snackText = 'Dialog opened'
      },
      closeUnits () {
        console.log('Dialog closed')
      },
    },
  }
</script>
<style scoped>
  .my-image {
    width: 350px;
    height: 250px;
    object-fit: cover;
    object-position: center; /* This centers the image within the element */
    border-radius: 25px;
  }
</style>