<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background}">
    <SideBar/>
    <TopBar/>
    <v-container  fluid style="background-color: #323B50" >
      <v-card flat color="#323B50" class="card_global flex-grow-0">
        <!-- <h2 class="mt-2" >Cash Sale Transaction</h2> -->
        <!--p class="pl-4">No Credit transactions.</p-->
        <v-row >
          <v-snackbar v-model="snack" :timeout="5000" :color="snackColor">
            {{ snackText }}
            <template v-slot:action="{ attrs }">
              <v-btn v-bind="attrs" text @click="snack = false">Close</v-btn>
            </template>
          </v-snackbar>
          <v-col cols="12" sm="8" class="pa-2">
            <!-- Show progress bar while products are loading -->
            <v-progress-linear v-if="!productsLoaded" indeterminate class="mb-4"></v-progress-linear>
            <!-- Show product list when products are loaded -->
            <!-- <v-card flat class="card_item pa-2" color="#323B50"> -->
              <v-list v-else color="#323B50" style="height: 570px; overflow-y: scroll; overflow-x: hidden">
              <v-row>
                  <template v-if="$vuetify.breakpoint.smAndUp">
                    <v-col v-for="(product, i) in productInventory" :key="i" cols="12" sm="8" md="6" lg="4" xl="3">
                      <v-hover v-slot="{hover}" close-delay="200" >
                        <v-card class="mx-auto align-center" height="250" tile color="#333c51" @click="productSelect(product)">
                          <v-card-actions>
                              <v-btn tile small :color="hover ? 'orange--text' : '#777D8B'" dark>
                                {{ product.Quantity }}
                              </v-btn>
                          </v-card-actions>
                          <v-img :src="product.Image" width="150" height="150">
                            <v-expand-transition>
                                <div v-if="hover" class="d-flex transition-fast-in-fast-out orange darken-2 v-card--reveal text-h2 white--text" style="height: 100%;">
                                    {{product.SellingPrice}}
                                </div>
                              </v-expand-transition>
                          </v-img>
                          <v-card-text :class="hover ? 'orange--text' : 'white--text'">
                            {{product.ProductName}}
                          </v-card-text>
                        </v-card>
                      </v-hover> 
                    </v-col>
                  </template>
                  <template v-if="$vuetify.breakpoint.smAndDown">
                    <v-col v-for="(product, i) in productInventory" :key="i" cols="12" sm="6" md="4" lg="3" xl="2">
                    <v-hover v-slot="{hover}" close-delay="200">
                      <v-card :elevation="hover ? 16 :2" :class="{ 'on-hover' : hover}"
                      class="mx-auto align-center" height="250" tile color="#333c51" @click="productSelect(product)">
                      <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn tile small :color="hover ? '#F34F64' : '#777D8B'" dark>
                            {{product.SellingPrice}}
                          </v-btn>
                        </v-card-actions>
                        <v-img :src="product.Image" width="150" height="150"></v-img>
                        <v-card-text :class="hover ? 'red--text' : 'white--text'">
                          {{product.ProductName}}
                        </v-card-text>
                      </v-card>
                    </v-hover>
                  </v-col>
                  </template>
              </v-row>
            </v-list>
            <!-- </v-card> -->
          </v-col>
          <v-col cols="12" sm="4" v-if="$vuetify.breakpoint.smAndUp">
            <Cart/>
          </v-col>
        </v-row>
        <v-btn v-if="$vuetify.breakpoint.smAndDown" icon @click="showCart = !showCart">
          <v-icon color="white">mdi-cart</v-icon>
        </v-btn>
        <v-card v-if="showCart" class="my-2" flat color="#323B50">
          <Cart />
        </v-card>
      </v-card>
    </v-container>
  </v-app>
</template>

<script>
  import Cart from "../components/Cart";
  import SideBar from "../components/Sidebar";
  import TopBar from "../components/Topbar";
  export default {
    components: {Cart, SideBar, TopBar},
    data: () => ({
      max25chars: (string) => string.substring(0, 25),
      showCart: false,
      productsLoaded: false,
      snack: false,
      snackColor: '',
      snackText: ''
    }),
    computed: {
      cart: state => state.cart,
      productInventory() {
        return this.$store.getters.productInventory;
      },
    },
    created () {
      this.loadProducts();
      this.$store.dispatch("getInventory");

    },
    methods: {
      loadProducts() {
        // code to load products goes here
        this.productsLoaded = true;
      },
      async productSelect(product) {
        // Find the product in the cart
        const cartProduct = this.$store.state.cart.find(p => p.ProductName === product.ProductName);
        // Find the product in the productInventory array
        const inventoryProduct = this.productInventory.find(p => p.ProductName === product.ProductName);
        if (cartProduct) {
          if (inventoryProduct) {
            // Check if the number of units in the cart are equal to number of units in stock
            if (cartProduct.Qty === Number(inventoryProduct.Quantity)){
              
              // Display an error message if the number of units in the cart match those that are in stock
              this.snackColor = 'error'
              this.snackText = 'Error: Maximum number of units in stock reached. '
              this.snack = true
              return;
            }else{
              
              // If the number of units in the cart are less than units in stock then proceed increment units by one
              cartProduct.Qty++;
              cartProduct.TotalCost = cartProduct.UnitPrice * cartProduct.Qty;
              cartProduct.VAT = cartProduct.TotalCost * inventoryProduct.Tax/100;

              // Commit the pushProductToCart mutation to update the cart state
              this.$store.commit('pushProductToCart', cartProduct);
            }
          }
          
        } else {
          if (inventoryProduct) {
              // Check if the product is out of stock
              if (inventoryProduct.Quantity === '0'){
                // Display an error message if the product is out of stock
                this.snackColor = 'error'
                this.snackText = 'Error: Product Is Out of Stock. '
                this.snack = true
                return;
              }else{
                this.$store.commit('pushProductToCart', {
                  ProductName: product.ProductName,
                  UnitPrice: product.SellingPrice,
                  Qty: 1,
                  TotalCost: product.SellingPrice * 1,
                  VAT: product.SellingPrice * inventoryProduct.Tax/100
                });
              }
              
          }
        }
      }
    },
  }
</script>
