<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background}">
    <SideBar/>
    <TopBar/>
    <v-container  fluid style="background-color: #323B50" >
      <v-card flat color="#323B50" class="card_global flex-grow-0">
        <v-list subheader two-line color="transparent">
          <v-list-title class="text-h5 white--text ">Bill To: </v-list-title>
            <v-list-item >
                <v-col cols="12" md="4">
                    <v-select class="mt-6" v-model="customer" :items="customername" required label="Customer Name"></v-select>
                </v-col>
                <v-col cols="4" style="padding-top: 25px !important;">
                    <v-btn outlined @click="customerdialog = !customerdialog" color="white">ADD NEW</v-btn>
                    <v-dialog v-model="customerdialog" max-width="500px">
                        <v-card>
                        <v-card-title>
                            ADD NEW CUSTOMER DETAILS
                        </v-card-title>
                          <v-card-text>
                              <v-text-field v-model="newCustomerName" required label="Company/Customer Name"></v-text-field>
                              <v-text-field v-model="newPhoneNumber" required label="Phone Number"></v-text-field>
                              <v-text-field v-model="newAddress" required label="Address"></v-text-field>
                              <v-text-field v-model="newLocation" required label="Location"></v-text-field>
                              <v-text-field v-model="newBalance" required label="Opening Balance"></v-text-field>
                          </v-card-text>
                        <v-card-actions>
                            <v-btn color="primary" text @click="customerdialog = false">Close</v-btn>
                            <v-btn color="success" text @click="newCustomerCB()">Save</v-btn>
                        </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-col>
            </v-list-item>
        </v-list>
        <v-row >
          <v-col cols="12" sm="8" class="pa-2">
            <v-list color="#323B50" style="height: 570px; overflow-y: scroll; overflow-x: hidden">
              <v-row>
                  <template v-if="$vuetify.breakpoint.smAndUp">
                    <v-col v-for="(product, i) in productInventory" :key="i" cols="12" sm="8" md="6" lg="4" xl="3">
                      <v-hover v-slot="{hover}" close-delay="200">
                        <v-card :elevation="hover ? 16 :2" :class="{ 'on-hover' : hover}"
                        class="mx-auto align-center" height="250" tile color="#333c51" @click="productSelect(product)">
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn tile small :color="hover ? '#F34F64' : '#777D8B'" dark>
                              {{product.SellingPrice}}
                            </v-btn>
                          </v-card-actions>
                          <v-img :src="product.Image" width="150" height="150"></v-img>
                          <v-card-text :class="hover ? 'red--text' : 'white--text'">
                            {{product.ProductName}}
                          </v-card-text>
                        </v-card>
                      </v-hover>
                    </v-col>
                  </template>
                  <template v-if="$vuetify.breakpoint.smAndDown">
                    <v-col v-for="(product, i) in productInventory" :key="i" cols="12" sm="6" md="4" lg="3" xl="2">
                      <v-hover v-slot="{hover}" close-delay="200">
                        <v-card :elevation="hover ? 16 :2" :class="{ 'on-hover' : hover}"
                        class="mx-auto align-center" height="250" tile color="#333c51" @click="productSelect(product)">
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn tile small :color="hover ? '#F34F64' : '#777D8B'" dark>
                              {{product.SellingPrice}}
                            </v-btn>
                          </v-card-actions>
                          <v-img :src="product.Image" width="150" height="150"></v-img>
                          <v-card-text :class="hover ? 'red--text' : 'white--text'">
                            {{product.ProductName}}
                          </v-card-text>
                        </v-card>
                      </v-hover>
                    </v-col>
                  </template>
              </v-row>
            </v-list>
            <!-- </v-card> -->
          </v-col>
          <v-col cols="12" sm="4" v-if="$vuetify.breakpoint.smAndUp">
            <invoiceCart/>
          </v-col>
        </v-row>
        <v-btn v-if="$vuetify.breakpoint.smAndDown" icon @click="showCart = !showCart">
          <v-icon color="white">mdi-cart</v-icon>
        </v-btn>
        <v-card v-if="showCart" class="my-2" flat color="#323B50">
          <invoiceCart />
        </v-card>
      </v-card>
    </v-container>
  </v-app>
</template>

<script>
  import invoiceCart from "../components/invoiceCart";
  import SideBar from "../components/Sidebar";
  import TopBar from "../components/Topbar";
  import {db} from "../firebase";
  import { getDocs, collection, addDoc} from "@firebase/firestore";

  export default {
    components: {invoiceCart, SideBar, TopBar},
    data: () => ({
      max25chars: (string) => string.substring(0, 25),
      showCart: false,
      productsLoaded: false,
      customerdialog: false,
      snack: false,
      snackColor: '',
      snackText: '',
      customer: null,
      newCustomerName: null,
      newPhoneNumber: null,
      newAddress: null,
      newLocation: null,
      newBalance: null,
    }),
    
    computed: {
      cart: state => state.invoiceCart,
      productInventory() {
        return this.$store.getters.productInventory;
      },
    },
    created () {
      this.getCustomerName();
      this.$store.dispatch("getInventory");

    },
    methods: {
      async getCustomerName() {
          const querySnapshot = await getDocs(collection(db, "Customer"))
          querySnapshot.forEach((doc) => {
          this.customername.push(doc.data().Names)
          })
      },

      newCustomerCB() {
          addDoc(collection(db, "Customer"), {Names: this.newCustomerName, PhoneNumber: this.newPhoneNumber,Address: this.newAddress,Location: this.newLocation,OpeningBalance: this.newBalance});
          alert("Customer added successfully.");
          this.customerdialog = false
          this.getCustomerName()
      },

      async productSelect(product) {
        // Find the product in the cart
        const cartProduct = this.$store.state.invoiceCart.find(p => p.ProductName === product.ProductName);
        // Find the product in the productInventory array
        const inventoryProduct = this.productInventory.find(p => p.ProductName === product.ProductName);
        if (cartProduct) {
          if (inventoryProduct) {
            // If the product exists in the cart, increment the Qty
            cartProduct.Qty++;
            cartProduct.TotalCost = cartProduct.UnitPrice * cartProduct.Qty;
            cartProduct.VAT = cartProduct.TotalCost * inventoryProduct.Tax/100;

            // Commit the pushProductToCart mutation to update the cart state
            this.$store.commit('pushProductToInvoiceCart', cartProduct);
          } else {
            // If the product exists in the cart, increment the Qty
            cartProduct.Qty++;
            cartProduct.TotalCost = cartProduct.UnitPrice * cartProduct.Qty;
            cartProduct.VAT = cartProduct.TotalCost * 0;

            // Commit the pushProductToCart mutation to update the cart state
            this.$store.commit('pushProductToInvoiceCart', cartProduct);
          }
          
        } else {
          if (inventoryProduct) {
              this.$store.commit('pushProductToInvoiceCart', {
                ProductName: product.ProductName,
                UnitPrice: product.SellingPrice,
                Qty: 1,
                TotalCost: product.SellingPrice * 1,
                VAT: product.SellingPrice * inventoryProduct.Tax/100
              });
          } else {
            // If the product does not exist in the cart, add a new object to the cart
            this.$store.commit('pushProductToInvoiceCart', {
              ProductName: product.ProductName,
              UnitPrice: product.SellingPrice,
              Qty: 1,
              TotalCost: product.SellingPrice * 1,
              VAT: product.SellingPrice * 0
            });
          }
        }
      }
    },
  }
</script>
<style>
  /* width */
  ::-webkit-scrollbar {
    width: 10px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #fff;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #555;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  h2 {
    color: #fff !important;
    cursor: pointer !important;
  }
  p{
    color: #fff;
    cursor: pointer;
  }


</style>