<template>
  <v-card flat class="card_item pa-2 mt-2" elevation="2">
    <v-data-table :headers="headers" :items="selectedProducts" sort-by="ProductName" class="elevation-1">
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Production Items</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:[`item.ProductName`]="{ item }">
        {{ item.ProductName }}
      </template>
      <template v-slot:[`item.UnitPrice`]="{ item }">
        {{ item.UnitPrice }}
      </template>
      <template v-slot:[`item.Qty`]="{ item }">
        <v-edit-dialog :return-value.sync="item.Qty" large persistent @save="saveUnits" @cancel="cancelUnits" @open="openUnits" @close="closeUnits">
          <div>{{ item.Qty }}</div>
          <template v-slot:input>
            <div class="mt-4 text-h6">Update Units</div>
            <v-text-field v-model="item.Qty" label="Edit" single-line counter autofocus></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:[`item.TotalCost`]="{ item }">
        {{ item.TotalCost }}
      </template>
      <template v-slot:[`item.VAT`]="{ item }">
        {{ item.VAT }}
      </template>
      <template v-slot:[`item.minus`]="{ item }">
        <v-btn @click="decrementQuantity(item)" text icon color="black lighten-2">
          <v-icon small color="red">mdi-minus</v-icon>
        </v-btn>
      </template>
      <template v-slot:[`item.add`]="{ item }">
        <v-btn @click="incrementQuantity(item)"  text icon color="black lighten-2">
          <v-icon small color="red">mdi-plus</v-icon>
        </v-btn>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-btn @click="deleteItem(item)"  text icon color="black lighten-2">
          <v-icon small color="red">mdi-delete</v-icon>
        </v-btn>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">
          Reset
        </v-btn>
      </template>
    </v-data-table>
    <v-list>
      <v-footer padless>
        <v-col class="text-center" cols="12">
          <strong>Total Value: </strong>
          <strong>Ksh. {{ totalCost }}</strong>
        </v-col>
      </v-footer>
      <v-divider color="#3F495E" class="divider_tab"></v-divider>
      <v-btn color="#F34F64" dark class="withoutuppercase d-flex align-center" width="100%" @click="consumptionSave()">Move to Production</v-btn>
      
    </v-list>
  </v-card>
</template>
<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'

  export default {
    props: ['cartData', 'max25chars'],
    name: "consumptionCart",
    data: () => ({
      drawer: true,
      snack: false,
      snackColor: '',
      snackText: '',
      dialogDelete: false,
      // customername: [],
        headers: [
            { text: 'Product Name', align: 'start', sortable: true, value: 'ProductName' },
            { text: 'Unit Price', value: 'UnitPrice' },
            { text: '', value: 'minus', sortable: false },
            { text: 'Quantity', value: 'Qty' },
            { text: '', value: 'add', sortable: false },
            { text: 'Total Value', value: 'TotalCost' },
            { text: 'VAT', value: 'VAT' },
            { text: 'Delete', value: 'actions', sortable: false },
          ],
        
          defaultItem: {  
            name: '',
            unitPrice: 0,
            units: 0,
            totalcost: 0,
          },
    }),
    computed: {
      totalCost() {
        return this.$store.getters.totalCostConsumption;
      },
       ...mapGetters(['consumptionCart']),

      selectedProducts() {
         return this.$store.getters.consumptionCart;
      },
      cart: {
            get() {
              return this.cartData;
            },
            set(cart) {
              this.$emit('update:cartData', cart);
            },
          },

        formTitle () {
            return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
          },
        },
    
        watch: {
          dialog (val) {
            val || this.close()
          },
          dialogDelete (val) {
            val || this.closeDelete()
          },
        },

        created () {
          this.initialize()
        },
      
        methods: {
          ...mapActions(['saveConsumptionToFirestore']),

          incrementQuantity(item) {
            item.Qty++;
            item.TotalCost = item.UnitPrice * item.Qty;
            // Dispatch a Vuex action to update the quantity of the item in the store
            this.$store.dispatch('updateConsumptionQuantity', { item, quantity: item.Qty, totalcost: item.TotalCost });
          },

          decrementQuantity(item) {
            if (item.Qty > 1) {
              item.Qty--;
              item.TotalCost = item.UnitPrice * item.Qty;
              // Dispatch a Vuex action to update the quantity of the item in the store
              this.$store.dispatch('updateConsumptionQuantity', { item, quantity: item.Qty, totalcost: item.TotalCost });
            }
          },

          initialize () {
            this.products = []
          },

          async consumptionSave() {
            await this.saveConsumptionToFirestore(); // Save the cart data to the Firestore
          },

          editItem (item) {
            this.editedIndex = this.products.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
          },
    
          deleteItem (item) {
            item.selected = true

            // Find the index of the item in the local selectedProducts array
            //const index = this.selectedProducts.findIndex(product => product.ProductName === item.ProductName)

            // Dispatch a Vuex action to remove the item from the cart array in the store
            this.$store.dispatch('removeConsumptionItem', item)
            // item.selected = true
            // this.editedIndex = this.selectedProducts.indexOf(item)
            // this.editedItem = Object.assign({}, item)
            // this.dialogDelete = true
          },
    
          deleteItemConfirm (item) {
            // Find the index of the item in the local selectedProducts array
            //const index = this.selectedProducts.findIndex(product => product.ProductName === item.ProductName)

            // Dispatch a Vuex action to remove the item from the cart array in the store
            this.$store.dispatch('removeConsumptionItem', item)

            // Remove the item from the selectedProducts array
            // this.selectedProducts.splice(index, 1)
            this.closeDelete()
          },
    
          close () {
            this.dialog = false
            this.$nextTick(() => {
              this.editedItem = Object.assign({}, this.defaultItem)
              this.editedIndex = -1
            })
          },
    
          closeDelete () {
            this.dialogDelete = false
            this.$nextTick(() => {
              this.editedItem = Object.assign({}, this.defaultItem)
              this.editedIndex = -1
            })
          },
    
          save () {
            if (this.editedIndex > -1) {
              Object.assign(this.products[this.editedIndex], this.editedItem)
            } else {
              this.products.push(this.editedItem)
            }
            this.close()
          },
          saveUnits(item) {
            // Dispatch a Vuex action to update the quantity of the item in the store
              this.$store.dispatch('updateConsumptionQuantity', { item, quantity: item.Qty });
              this.snack = true
              this.snackColor = 'success'
              this.snackText = 'Data saved'
          },
          cancelUnits (item) {
            // Reset the item's quantity to the original value
              item.Qty = this.originalQuantity;
              this.snack = true
              this.snackColor = 'error'
              this.snackText = 'Canceled'
          },
          openUnits () {
            this.snack = true
            this.snackColor = 'info'
            this.snackText = 'Dialog opened'
          },
          closeUnits () {
            console.log('Dialog closed')
          },
        },
  }
</script>