<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background}">
    <SideBar/>
    <TopBar/>
    <v-container fluid >
      <template>
          <v-data-table :headers="headers" :items="desserts" :search="search" sort-by="calories" class="elevation-1">
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title>Raw Materials Records</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn outlined color="primary" dark class="mb-2" v-bind="attrs" v-on="on"> ADD RAW MATERIAL</v-btn>
                  </template>
                  <v-card>
                    <v-card-title>
                      <span class="text-h5">{{ formTitle }}</span>
                    </v-card-title>
                    <v-card-text>
                      <v-container style="background-color: white !important;">
                        <v-row>
                          <v-col cols="12" md="8">
                            <v-select v-model="editedItem.name" :items="products" required label="Raw Material"></v-select>
                          </v-col>
                          <v-col cols="4" style="padding-top: 25px !important;">
                            <v-btn outlined @click="productdialog = !productdialog">ADD NEW</v-btn>
                            <v-dialog v-model="productdialog" max-width="500px">
                              <v-card>
                                <v-card-title>
                                  ADD NEW RAW MATERIAL
                                </v-card-title>
                                <v-card-text>
                                  <v-text-field v-model="newProductName" required label="New Product Name"></v-text-field>
                                </v-card-text>
                                <v-card-actions>
                                  <v-btn color="primary" text @click="productdialog = false">Close</v-btn>
                                  <v-btn color="success" text  @click="newProductCB()">Save</v-btn>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-col>
                          <v-col cols="12" md="8">
                          <v-select v-model="editedItem.branch" label="Branch Name" :items="branches" required></v-select>
                          </v-col>
                          <v-col cols="4" style="padding-top: 25px !important;">
                            <v-btn outlined @click="branchdialog = !branchdialog">ADD NEW</v-btn>
                            <v-dialog v-model="branchdialog" max-width="500px">
                              <v-card>
                                <v-card-title>
                                  ADD NEW BRANCH NAME
                                </v-card-title>
                                <v-card-text>
                                  <v-text-field v-model ="newBranch" label="New Branch Name" required></v-text-field>
                                </v-card-text>
                                <v-card-actions>
                                  <v-btn color="primary" text @click="branchdialog = false">Close</v-btn>
                                  <v-btn color="success" text @click="newBranchCB()">Save</v-btn>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-col>
                          <!--v-col cols="12" sm="6" md="4"-->
                            <v-text-field v-model="editedItem.quantity" label="Quantity/Units"></v-text-field>
                          <!--/v-col-->
                          <!--v-col cols="12" sm="6" md="4"-->
                            <v-text-field v-model="editedItem.alert" label="Alert Level"></v-text-field>
                          <!--/v-col-->
                          <!--v-col cols="12" sm="6" md="4"-->
                            <v-text-field v-model="editedItem.price" label="Buy Price"></v-text-field>
                          <!--/v-col-->
                        </v-row>
                      </v-container>
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="primary" text @click="close">Cancel</v-btn>
                      <v-btn color="success" text @click="RawMaterialSave()">Save</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <v-dialog v-model="dialogDelete" max-width="500px">
                  <v-card>
                    <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                      <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-toolbar>
            </template>
            <template v-slot:[`item.calories`]="{ item }">
              <v-chip :color="getColor(item.calories)" dark>{{ item.calories }}</v-chip>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
              <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
            </template>
            <template v-slot:no-data>
              <v-btn color="primary" @click="initialize">Reset</v-btn>
            </template>
          </v-data-table>
        </template>
    </v-container>
  </v-app>
  </template>
<script>
  import SideBar from "../components/Sidebar";
  import TopBar from "../components/Topbar";
  import {db} from "../firebase";
  import { getDocs, collection, addDoc } from "@firebase/firestore";
  export default {
    components: {SideBar, TopBar},
    data: () => ({
      dialog: false,
      productdialog: false,
      branchdialog: false,
      dialogDelete: false,
      newProductName: null,
      newBranch: null,
      search: '',
      products: [],
      branches: [],
      headers: [
        {
          text: 'ProductName',
          align: 'start',
          sortable: false,
          value: 'name',
        },
        { text: 'Branch', value: 'branch' },
        { text: 'Quantity', value: 'quantity' },
        { text: 'Alert Level', value: 'alert' },
        { text: 'Buy Price', value: 'price' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        pname: null,
        branch: null,
        quantity: null,
        alert: null,
        price: null,
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'ADD RAW MATERIAL' : 'EDIT RAW MATERIAL ITEM'
      },
    },

    watch: {
      // dialog (val) {
      //   val || this.close()
      // },
      dialogDelete (val) {
        val || this.closeDelete()
      },

      dialog() {
        this.getProductname()
        this.getBranch()
      }
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.getRawMaterialInventory()
      },

      async getProductname() {
        const querySnapshot = await getDocs(collection(db, "ProductName"))
        querySnapshot.forEach((doc) => {
          this.products.push(doc.data().productname)
        })
      },

      newProductCB() {
        addDoc(collection(db, "ProductName"), {productname: this.newProductName});
        alert("ProductName added successfully.");
        this.productdialog = false
        this.getProductname()
      },

      async getBranch() {
        const querySnapshot = await getDocs(collection(db, "Branch"))
        querySnapshot.forEach((doc) => {
          this.branches.push(doc.data().branch)
        })
      },

      newBranchCB() {
        addDoc(collection(db, "Branch"), {branch: this.newBranch})
        .then(() => {
          alert("Branch added successfully.");
          this.branchdialog = false
          this.getBranch()
        })
        .catch(function(error) {
          //Handle errors here
          
          alert(error);
        });
        
      },

      async getRawMaterialInventory() {
        const querySnapshot = await getDocs(collection(db, "RawMaterial"))
        querySnapshot.forEach((doc) => {
          this.desserts.push(
            {
              name: doc.data().ProductName, 
              branch: doc.data().Branch,
              quantity: doc.data().Quantity, 
              alert: doc.data().Alert,
              price: doc.data().BuyPrice
            }, 
          )
        })
      },

      async RawMaterialSave() {
        addDoc(collection(db, "RawMaterial"), {ProductName: this.newTax, Branch: this.newTax, Quantity: this.newTax, Alert: this.newTax, BuyPrice: this.newTax});
        this.snackColor = 'success'
        this.snackText = 'Saved Successfully.'
        this.snack = true
        this.dialog = false
        this.getRawMaterialInventory()
      },

      getColor (calories) {
        if (calories > 400) return 'red'
        else if (calories > 200) return 'orange'
        else return 'green'
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>
  