<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background}">
    <SideBar/>
    <TopBar/>
    <v-container fluid >
      <v-card color="#323B50" class="my-2 card_global flex-grow-0">
        <h2>Work In Progress.</h2>
        <v-row>
          <v-col cols="12" sm="8" >
            <template>
              <v-data-table :headers="headers" :items="desserts" :search="search" :single-select="singleSelect" show-select item-key="name" sort-by="calories" class="elevation-1">
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>WIP Records</v-toolbar-title>
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details></v-text-field>
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px">
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn outlined color="primary" dark class="mb-2" v-bind="attrs" v-on="on"> ADD INVENTORY</v-btn>
                      </template>
                      <v-card>
                        <v-card-title>
                          <span class="text-h5">{{ formTitle }}</span>
                        </v-card-title>
                        <v-card-text>
                          <v-container style="background-color: white !important;">
                            <v-row>
                              <v-col cols="12" md="8">
                                <v-select v-model="editedItem.name" label="Product Name"></v-select>
                              </v-col>
                              <v-col cols="4" style="padding-top: 25px !important;">
                                <v-btn outlined @click="productdialog = !productdialog">ADD NEW</v-btn>
                                <v-dialog v-model="productdialog" max-width="500px">
                                  <v-card>
                                    <v-card-title>
                                      ADD NEW PRODUCT NAME
                                    </v-card-title>
                                    <v-card-text>
                                      <v-text-field label="New Product Name"></v-text-field>
                                    </v-card-text>
                                    <v-card-actions>
                                      <v-btn color="primary" text @click="productdialog = false">Close</v-btn>
                                      <v-btn color="success" text @click="productdialog = false">Save</v-btn>
                                    </v-card-actions>
                                  </v-card>
                                </v-dialog>
                              </v-col>
                              <v-col cols="12" md="8">
                              <v-select v-model="editedItem.name" label="Branch Name"></v-select>
                              </v-col>
                              <v-col cols="4" style="padding-top: 25px !important;">
                                <v-btn outlined @click="branchdialog = !branchdialog">ADD NEW</v-btn>
                                <v-dialog v-model="branchdialog" max-width="500px">
                                  <v-card>
                                    <v-card-title>
                                      ADD NEW BRANCH NAME
                                    </v-card-title>
                                    <v-card-text>
                                      <v-text-field label="New Branch Name"></v-text-field>
                                    </v-card-text>
                                    <v-card-actions>
                                      <v-btn color="primary" text @click="branchdialog = false">Close</v-btn>
                                      <v-btn color="success" text @click="branchdialog = false">Save</v-btn>
                                    </v-card-actions>
                                  </v-card>
                                </v-dialog>
                              </v-col>
                              <!--v-col cols="12" sm="6" md="4"-->
                                <v-text-field v-model="editedItem.calories" label="Quantity/Units"></v-text-field>
                              <!--/v-col-->
                              <!--v-col cols="12" sm="6" md="4"-->
                                <v-text-field v-model="editedItem.fat" label="Alert Level"></v-text-field>
                              <!--/v-col-->
                              <!--v-col cols="12" sm="6" md="4"-->
                                <v-text-field v-model="editedItem.carbs" label="Selling Price"></v-text-field>
                              <!--/v-col-->
                              <v-col cols="12" md="8">
                                <v-select v-model="editedItem.protein" label="Tax Rate"></v-select>
                              </v-col>
                              <v-col cols="4" style="padding-top: 25px !important;">
                                <v-btn outlined @click="taxdialog = !taxdialog">ADD NEW</v-btn>
                                <v-dialog v-model="taxdialog" max-width="500px">
                                  <v-card>
                                    <v-card-title>
                                      ADD TAX RATE
                                    </v-card-title>
                                    <v-card-text>
                                      <v-text-field label="New Tax Rate"></v-text-field>
                                    </v-card-text>
                                    <v-card-actions>
                                      <v-btn color="primary" text @click="taxdialog = false">Close</v-btn>
                                      <v-btn color="success" text @click="taxdialog = false">Save</v-btn>
                                    </v-card-actions>
                                  </v-card>
                                </v-dialog>
                              </v-col>
                            </v-row>
                          </v-container>
                        </v-card-text>

                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn color="primary" text @click="close">Cancel</v-btn>
                          <v-btn color="success" text @click="save">Save</v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                    <v-dialog v-model="dialogDelete" max-width="500px">
                      <v-card>
                        <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                          <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                          <v-spacer></v-spacer>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                  </v-toolbar>
                </template>
                <template v-slot:[`item.calories`]="{ item }">
                  <v-chip :color="getColor(item.calories)" dark>{{ item.calories }}</v-chip>
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
                  <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
                </template>
                <template v-slot:no-data>
                  <v-btn color="primary" @click="initialize">Reset</v-btn>
                </template>
              </v-data-table>
            </template>
          </v-col>
          <v-col cols="12" sm="4">
            <wipCart/>
          </v-col>
        </v-row>
      </v-card>
    </v-container>
  </v-app>
  </template>
<script>
  import wipCart from "../components/wipCart";
  import SideBar from "../components/Sidebar";
  import TopBar from "../components/Topbar";
  export default {
    components: {wipCart, SideBar, TopBar},
    data: () => ({
      dialog: false,
      productdialog: false,
      branchdialog: false,
      taxdialog: false,
      dialogDelete: false,
      search: '',
      singleSelect: false,
      selected: [],
      headers: [
        {
          text: 'Dessert (100g serving)',
          align: 'start',
          sortable: false,
          value: 'name',
        },
        { text: 'Calories', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'ADD INVENTORY' : 'EDIT INVENTORY ITEM'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.desserts = [
          {
            name: 'Frozen Yogurt',
            calories: 159,
            fat: 6.0,
            carbs: 24,
            protein: 4.0,
          },
          {
            name: 'Ice cream sandwich',
            calories: 237,
            fat: 9.0,
            carbs: 37,
            protein: 4.3,
          },
          {
            name: 'Eclair',
            calories: 262,
            fat: 16.0,
            carbs: 23,
            protein: 6.0,
          },
          {
            name: 'Cupcake',
            calories: 305,
            fat: 3.7,
            carbs: 67,
            protein: 4.3,
          },
          {
            name: 'Gingerbread',
            calories: 356,
            fat: 16.0,
            carbs: 49,
            protein: 3.9,
          },
          {
            name: 'Jelly bean',
            calories: 375,
            fat: 0.0,
            carbs: 94,
            protein: 0.0,
          },
          {
            name: 'Lollipop',
            calories: 392,
            fat: 0.2,
            carbs: 98,
            protein: 0,
          },
          {
            name: 'Honeycomb',
            calories: 408,
            fat: 3.2,
            carbs: 87,
            protein: 6.5,
          },
          {
            name: 'Donut',
            calories: 452,
            fat: 25.0,
            carbs: 51,
            protein: 4.9,
          },
          {
            name: 'KitKat',
            calories: 518,
            fat: 26.0,
            carbs: 65,
            protein: 7,
          },
        ]
      },
      getColor (calories) {
        if (calories > 400) return 'red'
        else if (calories > 200) return 'orange'
        else return 'green'
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>
  